<template>
    <div>
        <div class="banner animated tada">
            <p class="mb-0">
                <span class="mx-5 bold">Cada 3 sandwich</span>
                <span class="big-text animated tada">17% OFF</span>
            </p>
        </div>
        <header>
            <h1>Desayuno Express</h1>
        </header>
        <div class="products-container">
            <div
                class="sections-container mt-3 d-flex flex-column flex-md-row align-items-center"
                v-for="(product, idx) of products"
                :key="idx"
            >
                <section id="products-section" class="col-md-auto">
                    <h2>Productos</h2>
                    <div class="product">
                        <img :src="product.image" :alt="product.name" width="150" />
                        <p class="mb-2 mt-3">
                            <b>{{ product.name }}</b>
                        </p>
                        <p>{{ product.description }}</p>
                        <p class="price">
                            <i>{{ product.price }}</i>
                        </p>
                        <div class="amount-buttons">
                            <b-button class="remove" @click="removeProduct(product.id, -1)">-</b-button>
                            <span class="mx-2">{{ request.products[idx]?.amount || 0 }}</span>
                            <b-button class="add" @click="addProduct(product.id, 1)">+</b-button>
                        </div>
                    </div>
                    <!-- Agrega más productos según sea necesario -->
                </section>

                <hr class="d-md-none w-75 my-5" />

                <section id="customize-section" class="col-md-auto">
                    <h2>Agregar extras <span id="custom-sandwich-name"></span></h2>
                    <div class="ingredient" v-for="(extra, i) of product.extras" :key="i">
                        <img :src="extra.image" :alt="extra.name" width="150" />
                        <p>{{ extra.name }}</p>
                        <p class="price">
                            <i>{{ extra.price }}</i>
                        </p>
                        <div class="ingredient-buttons">
                            <b-button
                                class="remove"
                                :disabled="isOrderEmpty"
                                @click="updateIngredient(product.id, extra.name, -1)"
                                >-</b-button
                            >
                            <!-- <input
                                :id="extra.name + '-extra-counter'"
                                class="ingredient-counter"
                                type="number"
                                v-model="request.products[idx].extras[extra.name]"
                                readonly
                            /> -->
                            <span class="mx-2">{{ request.products[idx]?.extras[extra.name] || 0 }}</span>
                            <b-button
                                class="add"
                                :disabled="isOrderEmpty"
                                @click="updateIngredient(product.id, extra.name, 1)"
                                >+</b-button
                            >
                        </div>
                    </div>
                </section>
            </div>
            <div class="d-flex justify-content-end text-end bg-body-secondary mt-5 px-5 py-3" v-if="!isOrderEmpty">
                <div class="d-flex flex-column align-items-end">
                    <!-- <div v-html="orderSummary"></div> -->
                    <h2 class="bold">Resumen del pedido</h2>
                    <div v-for="(product, idx) of request.products" :key="idx">
                        <p class="mb-2">
                            <b>{{ product.name }} - ({{ product.amount }}) x {{ product.price }}</b>
                        </p>
                        <ul class="mb-0">
                            <li v-for="(count, key) of product.extras" :key="key">
                                extra de {{ key }} - ({{ count }}) x {{ extraPrice(product.id, key) }}
                            </li>
                        </ul>
                    </div>
                    <hr class="w-100 mb-2" />
                    <p class="mb-0">{{ totalCost }}</p>
                </div>
            </div>
            <div class="w-100">
                <b-button class="buy-button-block" :disabled="isOrderEmpty" @click="buyNow">Hacer pedido</b-button>
            </div>
        </div>
    </div>
</template>

<script>
import Vue from 'vue';

export default {
    name: 'HelloWorld',
    props: {
        msg: String,
    },
    data() {
        return {
            products: [
                {
                    id: 1,
                    name: 'Sándwich Clásico',
                    image: 'https://t1.uc.ltmcdn.com/es/posts/0/6/9/como_hacer_un_sandwich_mixto_33960_orig.jpg',
                    description: 'Jamon, jamon, queson, jamon',
                    price: '2.00€',
                    ingredients: { Jamón: 3, Queso: 1 },
                    extras: [
                        {
                            id: 1,
                            name: 'Jamón',
                            image: 'https://s03.s3c.es/imag/_v0/1200x655/1/0/2/jamon-codido-iStock-488330599.jpg',
                            price: '0.25€',
                        },
                        {
                            id: 2,
                            name: 'Queso',
                            image: 'https://i0.wp.com/meatlovers.es/wp-content/uploads/queso-sandwich-lonchas_873112.jpg?fit=2000%2C1128&ssl=1',
                            price: '0.30€',
                        },
                        {
                            id: 3,
                            name: 'Zumo',
                            image: 'https://prod-mercadona.imgix.net/images/bce25e5f34a735d4ff9f35012eb7e051.jpg?fit=crop&h=600&w=600',
                            price: '0.50€/u',
                        },
                        // {
                        //     id: 3,
                        //     name: 'Ketchup',
                        //     image: 'https://supercostablanca.es/4066-home_default/heinz-tomato-ketchup-570g.jpg',
                        //     price: '0.10€'
                        // },
                    ],
                },
            ],
            request: {
                name: '',
                products: [
                    {
                        id: 1,
                        name: 'Sándwich Clásico',
                        description: 'Jamon, jamon, queson, jamon',
                        amount: 1,
                        price: '2.00€',
                        ingredients: { Jamón: 3, Queso: 1 },
                        extras: {},
                    },
                ],
            },
        };
    },
    computed: {
        isOrderEmpty() {
            return this.request.products.length === 0;
        },
        orderSummary() {
            let html = this.request.products.length ? '<h2>Resumen del pedido:</h2>' : '';
            for (const product of this.request.products) {
                let extras = '';
                for (const key in product.extras) {
                    const amount = product.extras[key];
                    extras += `<li>${amount} extra de ${key}</li>`;
                }
                html += `
                <div>
                    <p><b>${product.amount} - ${product.name}</b></p>
                    <ul>${extras}</ul>
            </div>`;
            }
            return html;
        },
        totalCost() {
            let total = 0;
            let sandwichs = 0;
            for (let product of this.request.products) {
                sandwichs += product.amount;
                let price = product.price.substring(0, product.price.indexOf('€'));
                total += Number(price).valueOf() * product.amount;
                for (const key in product.extras) {
                    const count = product.extras[key];
                    let extraPrice = this.extraPrice(product.id, key);
                    if (extraPrice) {
                        extraPrice = extraPrice.substring(0, extraPrice.indexOf('€'));
                        total += count * extraPrice;
                    }
                }
            }
            if (sandwichs >= 3) total = total - (total * (Math.round(sandwichs / 3) * 17)) / 100;
            return Number(total).toFixed(2) + '€';
        },
    },
    methods: {
        extraPrice(productId, extra) {
            return this.products.find((p) => p.id === productId)?.extras?.find((e) => e.name === extra)?.price;
        },

        buyNow() {
            // let message = `Hola, te escribe ${request.name}, quiero,\n\n`;
            let message = `*Nuevo pedido*\n\n`;
            for (const product of this.request.products) {
                let extras = '';
                for (const key in product.extras) {
                    const extra = product.extras[key];
                    extras += `- ${extra} extra de ${key}\n`;
                }
                if (!extras.length) {
                    message += `(${product.amount}) "${product.name}"\n`;
                } else {
                    message += `(${product.amount}) "${product.name}" con:\n`;
                }
                message += extras;
            }
            const encodedMessage = encodeURIComponent(message);
            window.open(`https://wa.me/?text=${encodedMessage}`, '_blank');
        },

        customize(productName, id) {
            document.getElementById('customize-section').style.display = 'block';
            document.getElementById('custom-sandwich-name').textContent = productName;
            document.getElementById('customize-sandiwich' + id).style.display = 'none';
            document.getElementById('cancel-customize-sandiwich' + id).style.display = 'inline-block';
        },

        cancelCustomize(id) {
            // document.getElementById('customize-section').style.display = 'none';
            // document.getElementById('customize-sandiwich' + id).style.display = 'inline-block';
            // document.getElementById('cancel-customize-sandiwich' + id).style.display = 'none';
            const product = this.products.find((item) => item.id === id);
            const requestProduct = this.request.products.find((item) => item.id === id);
            requestProduct.extras = product.ingredients;
        },

        addProduct(id, change) {
            const requestProduct = this.request.products.find((item) => item.id === id);
            if (!requestProduct) {
                const p = this.products.find((item) => item.id === id);
                console.log('p: ', p);
                if (p) this.request.products.push({ ...p, amount: 1, extras: {} });
            } else {
                requestProduct.amount += change;
            }
            console.log('this.addProduct: ', this.request);
            // document.getElementById('counter-product' + id).value = requestProduct.amount;
            // document.getElementById('purchase-buttons').style.display = 'block';
        },

        removeProduct(id, change) {
            const requestProduct = this.request.products.find((item) => item.id === id);
            if (requestProduct && requestProduct.amount > 0) {
                requestProduct.amount += change;
                // document.getElementById('counter-product' + id).value = requestProduct.amount;
                if (requestProduct.amount === 0) {
                    const requestProductIndex = this.request.products.findIndex((item) => item.id === id);
                    if (requestProductIndex !== -1) {
                        this.request.products.splice(requestProductIndex, 1);
                        // document.getElementById('purchase-buttons').style.display = 'none';
                    }
                }
            }
            console.log('this.removeProduct: ', this.request);
        },

        updateIngredient(id, ingredient, change) {
            console.log('ingredient: ', ingredient);
            const requestProduct = this.request.products.find((item) => item.id === id);
            console.log('requestProduct: ', requestProduct);
            if (!requestProduct.extras[ingredient]) Vue.set(requestProduct.extras, ingredient, 0);
            if (requestProduct.extras[ingredient] > 0 || (requestProduct.extras[ingredient] === 0 && change !== -1)) {
                requestProduct.extras[ingredient] += change;
                console.log('requestProduct.extras: ', requestProduct.extras);
            }
            // document.getElementById(ingredient + '-extra-counter').value = requestProduct.extras[ingredient];
        },
    },
};
</script>

